<style lang="less">
    @import 'login.less';
</style>

<template>
    <div class="login">
        <div class="lizi">
            <div id="particles-js"></div>
        </div>
        <div class="login-con">
            <Card icon="log-in" :bordered="false">
                <h2 slot="title" class="zd-common-tc zd-common-font-weight">青蚕数据互操作平台</h2>
                <div class="form-con">
                    <Form ref="form" :model="form" :rules="rules" @keydown.enter.native="handleSubmit">
                        <FormItem prop="userName">
                            <Input v-model.trim="form.userName" placeholder="请输入用户名" :maxlength="20">
                            <span slot="prepend">
                              <Icon :size="16" type="ios-person"></Icon>
                            </span>
                            </Input>
                        </FormItem>
                        <FormItem prop="password">
                            <Input type="password" v-model.trim="form.password" placeholder="请输入密码" :maxlength="20" @paste.native.capture.prevent="copyHandle">
                            <span slot="prepend">
                              <Icon :size="14" type="md-lock"></Icon>
                            </span>
                            </Input>
                        </FormItem>
                        <FormItem prop="isPassVerify">
                            <drag-verify
                                    :width="270"
                                    :height="36"
                                    text="向右滑动验证"
                                    :circle="false"
                                    textSize="16px"
                                    progressBarBg="#1c90f3"
                                    successText="验证成功"
                                    @passcallback="verifyHandle"
                            ></drag-verify>
                        </FormItem>

                        <FormItem>
                            <Button @click="handleSubmit('form')" type="primary" long>登录</Button>
                        </FormItem>
                    </Form>
                </div>
            </Card>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery'
    import dragVerify from 'vue-drag-verify'
    import '../../../../node_modules/_particles.js@2.0.0@particles.js/particles'
    import dataConfig from '@comm/particles/data.json'
    import {utils} from "../../script/utils";

    export default {
        components: {
            dragVerify
        },
        data() {
            const validatePassword = (rule, value, callback) => {
                if (value !== "") {
                    if (utils.regPwd.test(value)) {
                        callback();
                    }else{
                        callback(new Error(utils.regPwdMsg));
                    }
                } else {
                    callback(new Error('请填写密码'));
                }
            };
            return {
                form: {
                    userName: '',
                    password: '',
                    isPassVerify: '',
                },
                rules: {
                    userName: [
                        {required: true, message: '请填写用户名', trigger: 'blur'}
                    ],
                    password: [
                        {required: true, validator: validatePassword, trigger: 'blur'}
                    ],
                    isPassVerify: [
                        {required: true, message: '向右滑动进行验证', trigger: 'blur'}
                    ]
                }
            }
        },
        computed: {},
        created(){},
        methods:{
            //验证
            verifyHandle() {
                this.form.isPassVerify = 'true';
                this.$refs['form'].validate();
            },
            handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid && this.form.isPassVerify !== ''){
                        this.$router.replace('/');
                    }
                })
            },
            // 禁止粘贴
            copyHandle(){
                return false;
            }
        },
        mounted(){
            $(function () {
                particlesJS('particles-js', dataConfig, function() {
                    // console.log('callback - particles.js config loaded');
                });
            })
        }
    }
</script>

<style class="less" scoped>
    .lizi {
        width: 100%;
        height: 100%;
    }
    #particles-js{
        width: 100%;
        height: 100%;
        position: relative;
        margin-left: auto;
        margin-right: auto;
    }
</style>



